*{margin: 0; padding: 0;}
body{background-color: #eff3f7; height: 100%; font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    .container{padding: 100px 30px; box-sizing: border-box;
        .loader{display: flex; justify-content: center; align-items: center; height: 100vh; font-size: 2rem; color: rgb(250, 136, 5);
            .loader_text{animation: ain 1s infinite alternate; opacity: 0;}
        }
    }
}
@keyframes ain {
    0%{opacity: 0;}
    100%{opacity: 1;}
}